// Title font
@font-face {
  font-family: "ChakraPetch";
  src: url("../../assets/fonts/ChakraPetch-Bold.woff2") format("woff2"),
    url("../../assets/fonts/ChakraPetch-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ChakraPetch";
  src: url("../../assets/fonts/ChakraPetch-SemiBold.woff2") format("woff2"),
    url("../../assets/fonts/ChakraPetch-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ChakraPetch";
  src: url("../../assets/fonts/ChakraPetch-Medium.woff2") format("woff2"),
    url("../../assets/fonts/ChakraPetch-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ChakraPetch";
  src: url("../../assets/fonts/ChakraPetch-Regular.woff2") format("woff2"),
    url("../../assets/fonts/ChakraPetch-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

