@import '../variables';
.terms {
	&-calendar {
		display: flex;
		align-items: center;
		&-wrapper {
			margin-top: 40px;
		}
		& span {
			display: inline-block;
			font-family: $title-font;
			color: $white;
			font-size: 2.6rem;
			line-height: calc(32 / 26);
			font-weight: 700;
			margin-right: 12px;
		}
	}
	&-wrapper {
		padding: 144px 0;
		padding-bottom: 68px;
		position: relative;
		background: $dark-700;
		& img[data-img='rock-1'] {
			position: absolute;
			pointer-events: none;
			right: 50%;
			bottom: 96px;
			height: 132px;
			transform: translateX(50%) translateX(627px);
			filter: drop-shadow(0px 0px 20px rgba(#083982, 0.3));
		}
	}
	&-title {
		margin-bottom: 72px;
	}
	&-block {
		position: relative;
		z-index: 1;
		display: flex;
		justify-content: space-between;
		&__first,
		&__second {
			max-width: calc(((6 / 12) * 100%) - 12px);
			& p {
				font-size: 2rem;
				line-height: calc(28 / 20);
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.terms {
		&-wrapper {
			padding: 144px 0;
			padding-bottom: 60px;
			& img[data-img='rock-1'] {
				width: 69px;
				bottom: 47px;
				transform: translateX(50%) translateX(375px);
			}
		}
		&-title {
			margin-bottom: 40px;
		}
		&-block {
			&__first p,
			&__second p {
				font-size: 1.4rem;
				line-height: calc(22 / 14);
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.terms {
		&-calendar {
			&-wrapper {
				margin-top: 30px;
			}
			& span {
				font-size: 1.8rem;
				line-height: calc(26 / 18);
				margin-right: 10px;
			}
			& img {
				max-width: 90px;
				max-height: 90px;
			}
		}
		&-wrapper {
			padding: 88px 0;
			padding-bottom: 60px;
		}
		&-title {
			margin-bottom: 24px;
		}
		&-block {
			flex-direction: column;
			&__first,
			&__second {
				max-width: none;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.terms {
		&-block {
			&__first,
			&__second {
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
				}
			}
		}
	}
}
