* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  &.is-locked {
    overflow: hidden;
    & body {
      width: 100%;
      top: 0;
      left: 0;
      overflow: hidden;
    }
  }
}
body {
  overflow-x: hidden;
}
#app {
  overflow: hidden;
}
a {
  text-decoration: none;
}

