.mint {
	background: $dark-500;
	position: relative;
	&-input {
		font-family: $font;
		border-radius: 8px;
		border: 2px solid rgba($white, 0.1);
		background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
		box-shadow: 0px 0px 30px 0px #45308f99;
		text-align: center;
		color: $white;
		font-weight: 400;
		font-size: 1.6rem;
		line-height: calc(24 / 16);
	}
	&-content {
		&__wrapper {
			padding: 192px 0;
			padding-bottom: 120px;
			position: relative;
			& img[data-img='red-1'] {
				height: 61px;
				position: absolute;
				pointer-events: none;
				top: 182px;
				right: 95px;
			}
			& img[data-img='blue-1'] {
				position: absolute;
				pointer-events: none;
				top: 300px;
				left: 100px;
				height: 120px;
				filter: blur(7px);
			}
			& img[data-img='mint-monkey'] {
				position: absolute;
				pointer-events: none;
				bottom: -10px;
				left: -128px;
				z-index: 1;
			}
		}
		&__title {
			margin-bottom: 24px;
		}
		&__subtitle {
			margin: 0;
			margin-bottom: 36px;
		}
		&__block {
			max-width: 71.9444444%;
			margin: 0 auto;
			padding: 72px 105px;
			background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
			border: 2px solid rgba($white, 0.1);
			border-radius: 20px;
			box-shadow: 0px 0px 30px 0px #45308f99;
			text-align: center;
			position: relative;
			& img[data-img='green-1'] {
				position: absolute;
				pointer-events: none;
				height: 44px;
				left: 106px;
				bottom: 182px;
			}
			& img[data-img='blue-2'] {
				position: absolute;
				pointer-events: none;
				height: 90px;
				right: 82px;
				bottom: 164px;
			}
		}
		&__count {
			margin-bottom: 40px;
			display: flex;
			justify-content: center;
			&-btn {
				padding: 16px;
			}
			&-input {
				margin: 0 24px;
				min-width: 184px;
				min-height: 48px;
				&::-webkit-outer-spin-button,
				&::-webkit-inner-spin-button {
					-webkit-appearance: none;
					margin: 0;
				}
			}
		}
		&__auction {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 40px;
			&-info {
				max-width: calc(50% - 12px);
				flex-grow: 1;
			}
			&-list {
				list-style: none;
				text-align: left;
				position: relative;
			}
			&-item {
				font-family: $font;
				font-size: 1.6rem;
				line-height: calc(24 / 16);
				font-weight: 500;
				text-transform: uppercase;
				margin-bottom: 16px;
				&:last-child {
					margin-bottom: 0;
				}
				& span {
					font-weight: 600;
				}
			}
		}
		&__form {
			max-width: calc(50% - 12px);
			flex-grow: 1;
			background: radial-gradient(57.44% 57.44% at 50% 50%, #372181 0%, #251363 100%);
			border: 1px solid $white;
			border-radius: 10px;
			padding: 40px;
			padding-bottom: 50px;
			& .mint-content {
				&__count:first-child {
					& input {
						margin: 0;
						min-width: 290px;
					}
				}
				&__btn-wrapper {
					margin-bottom: 0;
				}
			}
		}
		&__btn {
			padding: 1em 4.5833em;
			background: radial-gradient(75% 75% at 50% 50%, #ffc902 0%, #ff8536 100%);
			&-wrapper {
				margin-bottom: 55px;
			}
		}
		&__note {
			& p {
				margin: 0;
				font-size: 1.6rem;
				line-height: calc(24 / 16);
			}
		}
	}
	&-modal {
		width: 45.8333%;
		position: fixed;
		z-index: 100;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		display: none;
		&_active {
			display: block;
		}
		&__wrapper {
			min-height: 336px;
			position: relative;
			background: $dark-700;
			padding: 96px 70px;
			border: 1px solid $white;
			border-radius: 10px;
			text-align: center;
			display: flex;
			flex-direction: column;
		}
		&__title {
			font-size: 2.4rem;
			line-height: calc(32 / 24);
			margin-bottom: 16px;
		}
		&__subtitle {
			font-size: 1.8rem;
			line-height: calc(26 / 18);
			font-weight: 600;
			margin-bottom: 32px;
		}
		&__btn {
			&-wrapper {
				margin-top: auto;
			}
		}
		&__close {
			width: 25px;
			height: 25px;
			position: relative;
			border: none;
			background: transparent;
			cursor: pointer;
			&-wrapper {
				position: absolute;
				top: 24px;
				right: 24px;
			}
			& span {
				width: 15px;
				height: 1px;
				position: absolute;
				background: $white;
				border-radius: 10px;
				&:first-child {
					transform: translateX(-7px) rotate(45deg);
				}
				&:last-child {
					transform: translateX(-7px) rotate(-45deg);
				}
			}
		}
	}
}

@media screen and (max-width: 1700px) {
	.mint {
		&-content {
			&__title {
				font-size: 5.6rem;
				line-height: calc(64 / 56);
			}
			&__wrapper {
				& img[data-img='mint-monkey'] {
					left: -198px;
				}
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.mint {
		&-content {
			&__title {
				margin-bottom: 16px;
			}
			&__subtitle {
				font-size: 2.4rem;
				line-height: calc(32 / 24);
				margin-bottom: 24px;
			}
			&__block {
				padding: 72px 105px 50px 105px;
			}
			&__count {
				margin-bottom: 32px;
			}
			&__form {
				padding-bottom: 44px;
			}
			&__btn {
				&-wrapper {
					margin-bottom: 40px;
				}
			}
			&__note {
				& p {
					font-size: 1.2rem;
					line-height: calc(20 / 12);
				}
			}
		}
		&-modal {
			width: 100%;
			max-width: 662px;
		}
	}
}

@media screen and (max-width: 1200px) {
	.mint {
		&-content {
			&__block {
				max-width: 80%;
			}
			&__wrapper {
				& img[data-img='red-1'] {
					z-index: 1;
					top: 220px;
					right: 30px;
				}
				& img[data-img='mint-monkey'] {
					left: -265px;
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.mint {
		&-content {
			&__wrapper {
				padding: 140px 0;
				padding-bottom: 64px;
				& img[data-img='red-1'] {
					top: 220px;
					height: 40px;
				}
				& img[data-img='mint-monkey'] {
					display: none;
				}
			}
			&__title {
				font-size: 3.2rem;
				line-height: calc(40 / 32);
				max-width: 500px;
				margin: 0 auto;
				margin-bottom: 24px;
			}
			&__subtitle {
				margin-bottom: 18px;
			}
			&__note {
				position: relative;
				z-index: 2;
			}
			&__btn {
				&-wrapper {
					margin-bottom: 32px;
				}
			}
			&__block {
				padding: 48px;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.mint {
		&-content {
			&__block {
				max-width: 540px;
				& img[data-img='green-1'] {
					display: none;
				}
				& img[data-img='blue-2'] {
					height: 55px;
					left: 24px;
					top: 186px;
				}
			}
			&__subtitle {
				max-width: 400px;
				font-size: 1.8rem;
				line-height: calc(26 / 18);
				margin: 0 auto;
				margin-bottom: 18px;
			}
			&__count {
				margin-bottom: 24px;
				&-input {
					min-height: auto;
					height: 40px;
					max-height: 100%;
					min-width: 164px;
					margin: 0 14px;
				}
				&-btn {
					padding: 14px;
				}
			}
		}
		&-modal {
			max-width: 530px;
			&__wrapper {
				min-height: 270px;
				padding: 56px 40px 64px 40px;
			}
			&__title {
				max-width: 280px;
				margin: 0 auto;
				margin-bottom: 16px;
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.mint {
		&-content {
			&__block {
				max-width: calc(100% - 48px);
				padding: 48px 24px;
				& img[data-img='blue-2'] {
					left: 14px;
				}
			}
			&__wrapper {
				& img[data-img='red-1'] {
					height: 23px;
					top: 160px;
					right: 37px;
				}
			}
			&__count {
				&-input {
					min-width: auto;
					width: 100%;
					max-width: 200px;
				}
				&-btn {
					flex-shrink: 0;
				}
			}
			&__btn {
				padding: 1em 4em;
			}
		}
		&-modal {
			max-width: calc(100% - 48px);
			&__wrapper {
				min-height: 270px;
				padding: 56px 24px 64px 24px;
			}
			&__btn {
				padding: 1em 3.4em;
			}
			&__close {
				width: 20px;
				height: 20px;
				&-wrapper {
					top: 16px;
					right: 16px;
				}
				& span {
					height: 2px;
					background: $gray-400;
					&:first-child {
						transform: translateX(-8px) rotate(45deg);
					}
					&:last-child {
						transform: translateX(-8px) rotate(-45deg);
					}
				}
			}
		}
	}
}
