.claim {
	& .mint {
		&-content {
			&__title {
				margin: 0 auto;
				margin-bottom: 24px;
			}
			&__count {
				&-input {
					min-width: 290px;
				}
			}
			&__block {
				& img[data-img='green-1'] {
					bottom: 136px;
					left: 62px;
				}
				& img[data-img='blue-2'] {
					bottom: 220px;
					right: 82px;
				}
			}
		}
	}
	& .partners {
		&-wrapper {
			& img[data-img='blue-1'] {
				bottom: 142px;
			}
			& img[data-img='green-1'] {
				bottom: 145px;
				right: 240px;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.claim {
		& .mint {
			&-content {
				&__title {
					max-width: 640px;
					margin-bottom: 16px;
				}
			}
		}
	}
}
