.whitelist {
	& .mint {
		&-content {
			&__title {
				margin: 0 auto;
				margin-bottom: 24px;
			}
			&__count {
				&-input {
					min-width: 290px;
				}
			}
			&__wrapper {
				& img[data-img='mint-monkey'] {
					bottom: 0;
					left: -115px;
				}
			}
			&__block {
				& img[data-img='green-1'] {
					bottom: 214px;
					left: 118px;
				}
				& img[data-img='blue-2'] {
					bottom: 220px;
					right: 82px;
				}
			}
		}
	}
	& .partners {
		&-wrapper {
			& img[data-img='blue-1'] {
				bottom: 142px;
			}
			& img[data-img='green-1'] {
				bottom: 145px;
				right: 240px;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.whitelist {
		& .mint {
			&-content {
				&__title {
					max-width: 640px;
					margin-bottom: 16px;
				}
				&__wrapper {
					& img[data-img='mint-monkey'] {
						bottom: 0;
						left: -135px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 1260px) {
	.whitelist {
		& .mint {
			&-content {
				&__wrapper {
					& img[data-img='mint-monkey'] {
						left: -180px;
					}
				}
				&__note {
					max-width: 88%;
					margin: 0 auto;
				}
			}
		}
	}
}

@media screen and (max-width: 991px) {
	.whitelist {
		& .mint {
			&-content {
				&__note {
					max-width: none;
				}
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.whitelist {
		& .mint {
			&-content {
				&__count {
					&-input {
						height: 48px;
						min-width: auto;
						margin: 0;
						width: 100%;
						max-width: 294px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 576px) {
	.whitelist {
		& .mint {
			&-content {
				&__block {
					& img[data-img='blue-2'] {
						bottom: auto;
						top: 100px;
					}
				}
			}
		}
		& .partners {
			&-wrapper {
				& img[data-img='blue-1'] {
					bottom: 180px;
				}
			}
		}
	}
}
