.auction {
	overflow: hidden;
	& .mint {
		&-content {
			&__title {
				max-width: 750px;
				margin: 0 auto;
				margin-bottom: 24px;
			}
			&__subtitle {
				margin-bottom: 40px;
			}
			&__block {
				max-width: none;
			}
			&__note {
				max-width: 1000px;
				margin: 0 auto;
			}
			&__wrapper {
				& img[data-img='red-1'] {
					top: 123px;
					right: 85px;
				}
				& img[data-img='red-2'] {
					position: absolute;
					pointer-events: none;
					height: 41px;
					bottom: 184px;
					left: 50%;
					transform: translateX(-92px) rotate(-60deg);
					filter: blur(7px);
				}
				& img[data-img='blue-1'] {
					top: 75px;
					left: 62px;
				}
				& img[data-img='blue-2'] {
					top: auto;
					bottom: 38px;
					left: 150px;
				}
				& img[data-img='mint-monkey'] {
					bottom: -24px;
					left: auto;
					right: -181px;
				}
			}
		}
	}
	& .partners {
		&-wrapper {
			& img[data-img='red-2'] {
				display: block;
			}
			& img[data-img='blue-1'] {
				display: none;
			}
			& img[data-img='red-1'] {
				bottom: 230px;
				right: 210px;
			}
			& img[data-img='green-1'] {
				left: 348px;
				bottom: 46px;
				right: auto;
			}
		}
	}
}

@media screen and (max-width: 1440px) {
	.auction {
		& .mint-content {
			&__title {
				margin-bottom: 16px;
				max-width: 520px;
			}
			&__block {
				padding: 64px 82px 56px 82px;
			}
			&__note {
				max-width: 820px;
			}
			&__wrapper {
				& img[data-img='mint-monkey'] {
					right: -231px;
				}
			}
		}
	}
}
