@import "../variables";
.btn {
  display: inline-block;
  border-radius: 100px;
  text-transform: uppercase;
  font-family: $font;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: calc(24 / 16);
  padding: 1em 2.375em;
  border: none;
  text-align: center;
  &_yellow {
    color: $dark-500;
    background: radial-gradient(75% 75% at 50% 50%, #ffc902 0%, #ff8536 100%);
    &:hover {
      background: radial-gradient(75% 75% at 50% 50%, #ffa800 0%, #ff6400 100%);
    }
    &:active {
      background: radial-gradient(75% 75% at 50% 50%, #ca8500 0%, #c74e00 100%);
    }
  }
  &_blue {
    color: $white;
    background: radial-gradient(75% 75% at 50% 50%, #4578e1 0%, #2828e0 100%);
    &:hover {
      background: radial-gradient(75% 75% at 50% 50%, #3067d7 0%, #0505b9 100%);
    }
    &:active {
      background: radial-gradient(75% 75% at 50% 50%, #1651cb 0%, #02029c 100%);
    }
  }
  &-minus {
    position: relative;
    border-radius: 8px;
    display: grid;
    place-items: center;
    width: 48px;
    height: 48px;
    cursor: pointer;
    & span {
      width: 16px;
      height: 4px;
      position: absolute;
      background: $white;
    }
  }
  &-plus {
    position: relative;
    border-radius: 8px;
    display: grid;
    place-items: center;
    width: 48px;
    height: 48px;
    cursor: pointer;
    & span {
      width: 16px;
      height: 4px;
      position: absolute;
      background: $white;
      &:last-child {
        transform: rotate(90deg);
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .btn {
    font-size: 1.2rem;
    line-height: calc(20 / 12);
  }
}

@media screen and (max-width: 767px) {
  .btn {
    &-plus,
    &-minus {
      width: 40px;
      height: 40px;
      & span {
        width: 12px;
        height: 3px;
      }
    }
  }
}
