.partners {
	&-wrapper {
		padding-bottom: 112px;
		position: relative;
		& img[data-img='red-1'] {
			height: 40px;
			position: absolute;
			pointer-events: none;
			bottom: 320px;
			right: 200px;
			transform: rotate(-60deg);
			filter: blur(8px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
		}
		& img[data-img='red-2'] {
			position: absolute;
			pointer-events: none;
			height: 61px;
			left: 207px;
			bottom: 235px;
			display: none;
		}
		& img[data-img='blue-1'] {
			position: absolute;
			pointer-events: none;
			bottom: 68px;
			left: 220px;
			height: 80px;
		}
		& img[data-img='green-1'] {
			position: absolute;
			pointer-events: none;
			bottom: 71px;
			right: 264px;
			height: 38px;
		}
	}
	&-title {
		margin-bottom: 56px;
		font-size: 40px;
		line-height: calc(48 / 40);
	}
	&-block {
		display: flex;
		justify-content: center;
	}
	&-item {
		display: flex;
		border-radius: 50%;
		margin-right: 56px;
		&:last-child {
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 991px) {
	.partners {
		&-wrapper {
			padding-bottom: 78px;
			& img[data-img='red-1'] {
				right: 130px;
			}
			& img[data-img='green-1'] {
				display: none;
			}
			& img[data-img='blue-1'] {
				left: 100px;
			}
		}
		&-title {
			margin-bottom: 32px;
		}
		&-item {
			max-width: 54px;
			max-height: 54px;
			margin-right: 38px;
			&:last-child {
				margin-right: 0;
			}
			& img {
				max-width: inherit;
				max-height: inherit;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.partners {
		&-wrapper {
			& img[data-img='red-1'] {
				bottom: 280px;
				height: 23px;
				filter: blur(6px) drop-shadow(0px 0px 74px rgba(254, 172, 65, 0.35));
			}
			& img[data-img='blue-1'] {
				height: 50px;
				bottom: 185px;
				left: 58px;
			}
		}
		&-title {
			font-size: 1.8rem;
			line-height: calc(26 / 18);
		}
	}
}

@media screen and (max-width: 576px) {
	.partners {
		&-wrapper {
			& img[data-img='red-1'] {
				right: 40px;
				bottom: 270px;
			}
		}
		&-block {
			column-gap: 15px;
			justify-content: space-between;
			max-width: 375px;
			margin: 0 auto;
		}
		&-item {
			margin: 0;
		}
	}
}
